
.homeBody {
    display: flex;
    flex-direction: column;
    font-family: Arial, sans-serif;
    background-color: #ffffff;
  }
  

  .topRow {
    display: flex;
    gap:20px;
    margin-top: 20px;
  }
  
  .topLeftDiv {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .topLeft {
    height: 400px;
    border-radius: 15px;
  }
  
  .intro {
    display: flex;
    flex-direction: column;
    position: relative;
    bottom: 400px;
    margin-left: 30px;
  }
  
  .welcome {
    font-size: 40px;
    font-weight: bold;
    color: rgb(10, 61, 10);
  }
  
  .subWelcome {
    font-size: 20px;
    color: #436445;
  }
  
  .button {
    background-color: rgb(60, 143, 60);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 25px;
    width: 120px;
    height: 40px;
    cursor: pointer;
    font-size: 17px;
    transition: transform 0.2s;
  }
  
  .button:hover {
    background-color: rgb(39, 102, 39);
    transform: scale(1.1);

  }
  
  .topRight {
    height: 400px;
    width: auto;
    border-radius: 15px;

  }
  

  .middleRow {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    position: relative;
    bottom: 300px;
  }
  
  .middleLeft,
  .middleMiddle,
  .middleRight {
    background-color: white;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 435px;
  }
  
  .middleLeftTop,
  .middleLeftLast {
    font-size: 20px;
    font-weight: bold;
  }
  
  .middleLeftMiddle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
  }
  
  .middleLeftMiddleLeft {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .arrowup,
  .redDown {
    width: 20px;
    height: 20px;
  }

  .greenGrow{
    height: 40px;
    width: 40px;
  }
  

  .lastRow {
    display: flex;
    gap: 20px;
    position: relative;
    bottom: 300px;
  }
  
  .bottomLeft,
  .bottomRight {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  .bottomLeft{
    width: 435px;
  }

  .bottomRight{
    width: 930px;
  }
  
  .currentDonwload,
  .areaInstalled {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .select {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
  }
  
  .pieChart {
    margin-top: 30px;
    position: relative;
    left:10%;
  }
  
  .divTop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  